import Nested from './nested';
import Parchment from 'parchment';
import Block from '../blots/block';
import Emitter from '../core/emitter'


export class SafeListItem extends Block {
  static formats(domNode) {
    return domNode.tagName === this.tagName ? undefined : super.formats(domNode);
  }

  format(name, value) {
    // console.log("safelistitem format", name, value)
    if (name === SafeList.blotName && !value) {
      this.replaceWith(Parchment.create(this.statics.scope));
    } else {
      super.format(name, value);
    }
  }

  remove() {
    console.log("safelistitem", "remove")
    if (this.prev == null && this.next == null) {
      this.parent.remove()
    } else {
      super.remove();
    }
  }

  insertBefore(blot, ref) {
    if (blot.statics.blotName == this.statics.blotName) {
      // this prevents inserting two containers recursively, instead we want them to join
      // e.g: everything we write is in the last created containers until switched off
      super.insertBefore(blot.children.head, ref);
    } else {
      super.insertBefore(blot, ref);
    }
  }

  replaceWith(name, value) {
    this.parent.isolate(this.offset(this.parent), this.length());
    if (name === this.parent.statics.blotName) {
      // this.parent.replaceWith(name, value); // This was add the second table-item on insert
      return this;
    } else {
      // here was an unwrap of this.parent - this unwrap broke link between SafeList and SafeListItem,
      // can't be here but we don't know its original purpose - what will brake?
      return super.replaceWith(name, value);
    }
  }

  // this means the element is numbered and increments numbering by one
  isNumbered() {
    return true
  }

  // fire a safe list change event, so we can compute numbers of items
  attach() {
    super.attach()
    window.quill.emitter.emit(Emitter.events.LIST_CHANGE)
  }

  detach() {
    super.detach()
    window.quill.emitter.emit(Emitter.events.LIST_CHANGE)
  }

  optimize() {
    super.optimize()
    window.quill.emitter.emit(Emitter.events.LIST_IN_PLACE)
  }
}
SafeListItem.blotName = 'safelistitem';
SafeListItem.tagName = 'safelistitem';

class SafeList extends Nested {

  static create(value) {
    let node = super.create(SafeList.tagName);
    node.setAttribute('mask', value)
    return node;
  }

  static formats(domNode) {
    return { [SafeList.blotName]: domNode.attributes['mask'].value };
  }

  // Userd by number generator
  mask() {
    return this.domNode.getAttribute('mask')
  }

  optimize() {
    super.optimize();

    // merge two consecutive containers
    // by our definition, there must be a divider between containers
    let next = this.next;
    if (next != null && next.prev === this &&
        next.statics.blotName === this.statics.blotName &&
        next.domNode.tagName === this.domNode.tagName &&
        next.mask() === this.mask()) {
      next.moveChildren(this);
      next.remove();
    }
  }

  formats() {
    return { [this.statics.blotName]: this.mask() }
  }

  // polymorphic part of replace in Nested
  // do only if the block is not already a safelist
  replaceBlock(target) {
    if (target.statics.blotName !== this.statics.blotName) {
      let item = Parchment.create(this.statics.defaultChild);
      target.moveChildren(item);
      this.appendChild(item);
    }
  }

  insertBefore(blot, ref) {
    if (blot instanceof SafeListItem) {
      super.insertBefore(blot, ref);
    } else {
      let index = ref == null ? this.length() : ref.offset(this);
      let after = this.split(index);
      after.parent.insertBefore(blot, after);
    }
  }

  // There is always a block as a leaf value, so if are trying to remove list from within another
  // container, we have to replace it with a new block - that is what Nested.replaceBlock does.
  removeContainer() {
    let isInsideContainer = Nested.isNestable(this.parent.statics.blotName)
    this.children.forEach((child) => {
      if (isInsideContainer) {
        this.parent.replaceBlock(child)
      } else {
        // shouldn't we create a new empty block here, too? It works so let's forget it now...
        child.moveChildren(this.parent, this)
      }
      child.remove()
    })
    this.remove();
  }
}

SafeList.blotName = 'safelist';
SafeList.tagName = 'safelist';
SafeList.defaultChild = 'safelistitem';

export default SafeList;



