import Inline from '../blots/inline';

class Annotation extends Inline {
  static create(value) {
    let node = super.create(value);
    let data;
    if (typeof value === 'string') {
      data = createData(value);
    } else {
      data = value;
    }
    node.setAttribute('data-id', data['data-id']);
    node.setAttribute('data-json', JSON.stringify(data['data-json']));
    return node;
  }

  static formats(domNode) {
    return {
      'data-id': domNode.attributes['data-id'].value,
      'data-json': JSON.parse(domNode.attributes['data-json'].value),
    };
  }

  format(name, value) {
    if (name !== this.statics.blotName || !value)
      return super.format(name, value);
    this.domNode.setAttribute('data-id', value['data-id']);
    this.domNode.setAttribute('data-json', JSON.stringify(value['data-json']));
  }

}
Annotation.blotName = 'annotation';
Annotation.tagName = 'avv-annotation';

function randomId() {
  return Math.random().toString(36).slice(2);
}

function createData(annotation) {
  return {
    'data-id': randomId(),
    'data-json': {
      messages: [
        {
          timestamp: new Date().toISOString(),
          username: $('#username').html(),
          text: annotation,
        },
      ],
    },
  };
}

export default Annotation;



