import Parchment from 'parchment';
import Container from './container';
import Block, { BlockEmbed } from './block';
import TextBlot from './text';
import List from '../formats/list';

class Nested extends Container {

  static create(value) {
    let node = super.create(this.tagName);
    return node;
  }

  static compare(self, other) {
    let selfIndex = Nested.order.indexOf(self);
    let otherIndex = Nested.order.indexOf(other);
    if (selfIndex >= 0 || otherIndex >= 0) {
      return selfIndex - otherIndex;
    } else if (self === other) {
      return 0;
    } else if (self < other) {
      return -1;
    } else {
      return 1;
    }
  }

  static isNestable(name) {
    return !!Nested.order.find((x) => x == name)
  }

  // // debugging purpose - pls delete
  // formatAt(index, length, name, value) {
  //   console.log(this.statics.blotName, "formatAt", index, length, name, value)
  //   if (this.children) {
  //     console.log(this.children.head, this.children.tail)
  //   } else {
  //     console.log("no children")
  //   }
  //   super.formatAt(index, length, name, value)
  // }

  // correct delta generation
  // this block is same for all nested
  replace(target) {
    // console.log(this.statics.blotName, "replace, this:", this)
    // console.log(this.statics.blotName, "replace, target:", target)

    // if it is another container, we don't want to replace it
    // instead - we just insert one into the other
    // we should test it for Nested.isNestable(target.statics.blotName), but for some reason, it doesn't work
    if (target.statics.blotName === 'safelistitem') {
      let safelist = target.parent
      safelist.parent.insertBefore(this, safelist.next);  // Scroll
      this.appendChild(safelist)
    } else {
      // no, it is not another container, we just replace it
      this.replaceBlock(target)
      if (target.parent == null) return;
      super.replace(target)
    }
  }

  // polymorphic part of replace, different for each descendant
  // this block must be different for SafeList
  replaceBlock(target) {
    let item = Parchment.create(this.statics.defaultChild);
    target.moveChildren(item);
    this.appendChild(item);
  }

  removeContainer() {
    this.moveChildren(this.parent, this);
    this.remove();
  }

  insertBefore(blot, ref) {
    // we don't really understand what's going on here, sometimes it just happens that blot
    // has no children, so we filtered that option out
    if (blot.statics.blotName == this.statics.blotName && blot.children.head) {
      // this prevents inserting two containers recursively, instead we want them to join
      // e.g: everything we write is in the last created containers until switched off
      super.insertBefore(blot.children.head, ref);
    } else {
      super.insertBefore(blot, ref);
    }
  }

  formats() {
    // correct delta generation
    let attributes = {}
    for(let i = 0; i < this.domNode.attributes.length; i++) {
      attributes[this.domNode.attributes[i].name] = this.domNode.attributes[i].value
    }
    return { [this.statics.blotName]: attributes }
  }

  static formats(domNode) {
    let attributes = {}
    for(let i = 0; i < domNode.attributes.length; i++) {
      attributes[domNode.attributes[i].name] = domNode.attributes[i].value
    }
    return attributes
  }

  optimize() {
    // console.log(this.statics.blotName, "optimize")
    super.optimize();

    if (this.parent instanceof Nested &&
        Nested.compare(this.statics.blotName, this.parent.statics.blotName) > 0) {
      let parent = this.parent.isolate(this.offset(), this.length());
      this.moveChildren(parent);
      parent.wrap(this);
    }
  }

  replaceWith(name, value) { // name = "condition", value = {name: "variable1"}
    // console.log(this.statics.blotName, "replaceWith", name, value)
    return super.replaceWith(name, value)
  };

}

Nested.blotName = 'dummy-container';
Nested.tagName = 'dummy-container';
Nested.scope = Parchment.Scope.BLOCK_BLOT;
Nested.defaultChild = 'block';
Nested.allowedChildren = [Block, BlockEmbed, TextBlot, List, Nested];
Nested.order = ['safelist', 'condition', 'locked']; // first is the lowest, last is the highest

export default Nested;



